
import { Options, Vue } from 'vue-class-component'
@Options({
  emits: ['networkChange'],
  data () {
    return {
      isRight: true
    }
  },
  props: {
    wallet: String,
    tokens: Array
  },
  watch: {
    wallet (v: string) {
      this.fetchBalance()
    }
  }
})
export default class WalletOnEth extends Vue {
  wallet!: string
  isRight!: boolean
  tokens!: { address: string, symbol: string }[]

  created () {
    if (!this.$eth.isMetaMask()) {
      return
    }
    this.startRun()
    const t = (parseInt(this.$eth.tId) === parseInt(this.$eth.chainId))
    this.$emit('networkChange', [t])
    this.isRight = t
  }

  async fetchBalance (): Promise<void> {
    if (!this.wallet) {
      return
    }
    const balance = await this.$eth.provider.getBalance(this.wallet)
    this.$store.commit('setBalance', {
      address: this.wallet,
      network: 'ethereum',
      symbol: 'ETH',
      amount: balance._hex
    })
    this.tokens.forEach(async (item) => {
      const token = this.$eth.getToken(item.address)
      const balance = await token.balance(this.wallet)
      this.$store.commit('setBalance', {
        address: this.wallet,
        network: 'ethereum',
        symbol: item.symbol,
        amount: balance._hex
      })
    })
  }

  async startRun (): Promise<void> {
    this.fetchBalance()
    this.$eth.provider.on('block', this.fetchBalance)
    this.$eth.eth.on('chainChanged', (chainId: string) => {
      const isRight = (parseInt(chainId) === parseInt(this.$eth.tId))
      console.log(isRight, chainId, this.$eth.tId)
      this.$emit('networkChange', [isRight])
    })
  }
}
