
import { Options, Vue } from 'vue-class-component'
import Metamask from './icons/Metamask.vue'
import Sync from './icons/Sync.vue'

@Options({
  events: ['connected'],
  data () {
    return {
      connecting: false,
      current: '',
      hasMetamask: false,
      error: {
        vet: '',
        eth: ''
      }
    }
  },
  props: {
    network: String
  },
  components: {
    Metamask,
    Sync
  }
})
export default class Connect extends Vue {
  network!: 'ethereum' | 'vechain'
  connecting!: boolean
  current!: 'ethereum' | 'vechain'
  hasMetamask!: boolean
  error!: {
    vet: string
    eth: string
  }

  async connectVechain () {
    this.error.vet = ''
    this.connecting = true
    this.current = 'vechain'
    try {
      const address = await this.$vet.connect()
      this.$store.commit('setAddress', { key: 'vechain', addr: address })
      this.$emit('connected')
    } catch (error: any) {
      this.error.vet = error.message
      console.log(error)
    } finally {
      this.connecting = false
    }
  }

  openMM () {
    window.open('https://metamask.io/download.html', '_blank')
  }

  created () {
    this.hasMetamask = this.$eth.isMetaMask()
  }

  async connectEth () {
    this.error.eth = ''
    this.connecting = true
    this.current = 'ethereum'
    try {
      const address = await this.$eth.connect()
      this.$store.commit('setAddress', { key: 'ethereum', addr: address })
      this.$emit('connected')
    } catch (error: any) {
      if (error.code === -32002) {
        this.error.eth = 'Error connecting'
      } else if (error.message !== 'abort') {
        this.error.eth = error.message
        console.error(error)
      }
    } finally {
      this.connecting = false
    }
  }
}
